importAll(require.context('./assets/img', false, /\.(svg|png|jpg|jpeg|gif)$/));
importAll(require.context('./assets/sass', false, /\.s[ac]ss$/i));
importAll(require.context('./assets/js', false, /\.js$/));

function importAll(r) {
    return r.keys().map(r);
}





